@import url('https://fonts.googleapis.com/css2?family=Nanum+Gothic&display=swap');

*{
  font-family: 'Nanum Gothic', sans-serif;
}

.Card {
  width: 330px;
  height: 180px;
  display: inline-block;
  padding: 15px;
  background-color: white;
  border-radius: 5px;
  border-bottom: 10px solid #a3daff;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 25px 50px -12px;
  text-align: center;
  align-items: center;
  justify-content: center;
  margin: 10px;
  &-Title {
    font-size: 1,25rem;
    color: #30A9DE;
    font-weight: 700;
  }
  &-Content {
    font-size: 2rem;
    color: black;
    margin-bottom: 15px;
  }
  &-Percent {
    font-size: 1.5rem;
  }
}
