*{font-family:'Nanum Gothic', sans-serif}.Card{width:330px;height:180px;display:inline-block;padding:15px;background-color:white;border-radius:5px;border-bottom:10px solid #a3daff;box-shadow:rgba(0,0,0,0.25) 0px 25px 50px -12px;text-align:center;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;margin:10px}.Card-Title{font-size:1,25rem;color:#30A9DE;font-weight:700}.Card-Content{font-size:2rem;color:black;margin-bottom:15px}.Card-Percent{font-size:1.5rem}

*{font-family:'Nanum Gothic', sans-serif}.Main{width:100%;height:100%;min-width:100vw;min-height:100vh;background-color:#e3dede;-webkit-justify-content:center;justify-content:center;text-align:center;-webkit-align-items:center;align-items:center;padding-top:30px}

*{font-family:'Nanum Gothic', sans-serif}.Country{width:100%;height:100%;background-color:#e3dede;-webkit-justify-content:center;justify-content:center;text-align:center;-webkit-align-items:center;align-items:center;padding-bottom:150px}.Country-Title{font-weight:700;font-size:2rem;margin-bottom:20px;color:#03a6ff;padding-top:35px}

